import React from 'react';
import { Link } from 'react-router-dom';
import { Dropdown, Menu } from 'semantic-ui-react'

 
const Header = () => {
  return(
    <Menu pointing secondary>
      <Link to ='/' className="item">
        Home
      </Link>
      <Dropdown item text="Notes">
        <Dropdown.Menu>
          <Dropdown item text='Teaching'>
              <Dropdown.Menu>
                  <Dropdown.Item >
                      <Link to ='/rn' className="item">
                       Interior point method example
                      </Link>                
                  </Dropdown.Item>
              </Dropdown.Menu>
          </Dropdown >
        </Dropdown.Menu>
      </Dropdown>
      <Dropdown item text="Toy Apps">
          <Dropdown.Menu>
              <Dropdown.Item>
                <Link to ='/biseccion' className="item">
                   Bisection Method
                </Link>        
              </Dropdown.Item>
              <Dropdown.Item>
                <Link to ='/newton' className="item">
                   Newton's Method
                </Link>        
              </Dropdown.Item>
              <Dropdown.Item>
                <Link to ='/vrp' className="item">
                   VRP
                </Link>        
              </Dropdown.Item>
          </Dropdown.Menu>
      </Dropdown>
    </Menu>
 )
}
export default Header;