import React from 'react';
import {BrowserRouter, Route} from 'react-router-dom';
import Header from './Header';
import Home from './Home';
import Notes from './Notes';
import BiseccionApp from './BiseccionApp';
import NewtonApp from './NewtonApp';
import VrpApp from './VrpApp';

const App = () => {

    return (
        <div>
            <BrowserRouter>
                <div className="ui container">
                    <Header/>
                    <Route path="/" exact component={Home}/>
                    <Route path="/rn" exact component={Notes}/>
                    <Route path="/biseccion" exact component={BiseccionApp}/>
                    <Route path="/newton" exact component={NewtonApp}/>
                    <Route path="/vrp" exact component={VrpApp}/>
                </div>
            </BrowserRouter>
        </div>

    );
}

export default App;